$container-width: 1000px;
//style
html{
	box-sizing: border-box;
	font-size: 16px;
	@include desktop {
		font-size: 16px;
	}

	@include laptop {
		font-size: 15px;
	}

	@include tablets {
		font-size: 14px;
	}

	@include phones {
		font-size: 13px;
	}
}

body,
html {
	width: 100%;
	height: 100%;
}

*, *:before, *:after{
	box-sizing: inherit;
}
body {
	font-family: Arial, sans-serif;
	line-height: 1.42;
	-webkit-font-smoothing: antialiased;
}
.wrapper {
	min-height: 100%;
	overflow: hidden;
	position: relative;
	min-width: 320px;
}
.container {
	min-height: 100%;
	max-width: $container-width;
	margin: 0 auto;
	@include desktop {
		width: 1000px;
	}

	@include laptop {
		width: 750px;
		padding: 0 rem(20px);
	}

	@include tablets {
		width: 480px;
		padding: 0 rem(20px);
	}

	@include phones {
		width: auto;
	}
}

li {
	list-style: none;
}

ul {
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: normal;
	margin: 0;
}
//reset input
input,
label,
select,
button,
textarea
{
	margin:0;
	border:0;
	padding:0;
	display:inline-block;
	vertical-align:middle;
	white-space:normal;
	background:none;
	line-height:1;

	font-size:13px;
	font-family:Arial;
}

input:focus
{
	outline:0;
}

